* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open sans', sans-serif;
  font-size: 20px;
}

div.App {
  display: flex;
  flex-direction: column;
  width: 90%;
  min-height: 100vh;
  margin: 0 auto;
  color: #222;
}

header {
  flex-basis: 30vh;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
}

aside {
  min-width: 240px;
}

section.Page {
  flex-grow: 1;
}

footer {
  background-color: #000;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  text-transform: uppercase;
}
header {
    position: relative;
    width: 100%;
    overflow: hidden;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    padding: 0 30px 30px 0;
}

nav.main ul {
    list-style-type: none;
}

nav.main ul li a {
    display: block;
    padding: 10px;
    text-decoration: none;
    color: #000;
    text-transform: uppercase;
    font-weight: 700;
    transition: .5s;
}

nav.main ul li a:hover {
    transform: translateX(20px);
}

nav.main ul li a.active {
    border-left: 10px solid #000;
    transform: translateX(0);
}

nav.main ul li:nth-child(1) a.active {
    border-color: #00e;
}

nav.main ul li:nth-child(2) a.active {
    border-color: #0e0;
}

nav.main ul li:nth-child(3) a.active {
    border-color: #e00;
}

nav.main ul li:nth-child(4) a.active {
    border-color: #0ee;
}

.contact textarea {
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}

.contact button {
    display: block;
    padding: 10px 20px;
    margin: 0 20% 0 auto;
    border: 2px solid black;
    background-color: #fff;
    cursor: pointer;
    font-size: 20px;
    transition: .5s;
}

.contact button:hover {
    color: #fff;
    background-color: #000;
}
.products h2 {
    margin-bottom: 20px;
}

.products ul {
    padding-left: 40px;
    list-style: square;
}

.products a {
    text-decoration: none;
    color: #333
}

.products li {
    margin-bottom: 20px;
}

.products a:hover {
    text-decoration: underline;
    color: #000
}
footer span {
    color: red;
}

footer p {
    display: inline;
    margin: 0 10px;
    font-size: 14px;
}
